*, *::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  background: linear-gradient(to right, rgb(120, 13, 190), rgb(54, 164, 255));
  color: white;
}

#playboard {
  display: grid;
  margin-top: 2rem;
  justify-content: center;
}

td {
  width: 50px;
  height: 50px;
  padding: 10px 10px;
  background-color: rgba(0, 0, 255, 0.24);
}

.pingl {
  background-color: rgb(105, 0, 167);
  cursor: pointer;
}

.move {
  background-color: rgba(0, 204, 255, 0.473);
  cursor: pointer;
}